import React, { useState } from 'react';
import GAL1 from "./img/1_gallery.jpg"
import GAL2 from "./img/2_gallery.jpg"
import GAL3 from "./img/3_gallery.jpg"
import GAL4 from "./img/4_gallery.jpg"
import GAL5 from "./img/5_gallery.jpg"
import GAL6 from "./img/6_gallery.jpg"
import '../Css/About.css'


const Gallery = () =>{
    return(
        <section className="gallery el-anim" id="gallery">
        <div className="container">
            <h2>    </h2>
            <div className="gallery-content">
            <div className="mini-gallery">
                <img
                data-desc="-, . , . , . 12, . "
                className="mini-img active"
                src={GAL1}
                alt="1_"
                />
                <img
                data-desc="-, . , . , . 13 "
                className="mini-img"
                src={GAL2}
                alt="2_"
                />
                <img
                data-desc=", . , . , . 25, 2 "
                className="mini-img"
                src={GAL3}
                alt="3_"
                />
                <img
                data-desc="-, . , .  , . 22"
                className="mini-img"
                src={GAL4}
                alt="4_"
                />
                <img
                data-desc=", . , . , . 1, . "
                className="mini-img"
                src={GAL5}
                alt="5_"
                />
                <img
                data-desc=", . , . , . 3"
                className="mini-img"
                src={GAL6}
                alt="6_"
                />
            </div>
            <div
                className="full-image"
                data-desc="-, . , . , . 12, . "
            >
                <img src={GAL1} alt="" />
            </div>
            </div>
        </div>
        </section>  
    )
    }
    
    export default Gallery